    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.music_play_page .music_head img {
    width: 62%;
    height: 62%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    border-radius: 50%;
    margin: auto;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}
.music_play_page .music_head img.picAuto {
      -webkit-animation: xz 10s linear 0.5s infinite;
              animation: xz 10s linear 0.5s infinite;
}
@-webkit-keyframes xz {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
