
.photo_page .photo_page_box {
  padding: 0.2rem;
}
.photo_page .photo_page_box .photo_list {
    margin-bottom: 0.2rem;
}
.photo_page .photo_page_box .photo_list .title {
      line-height: 0.6rem;
      word-break: break-all;
      word-wrap: break-word;
}
.photo_page .photo_page_box .photo_list .img {
      font-size: 0.28rem;
}
.photo_page .photo_page_box .photo_list .img img {
        width: 100%;
}
.photo_page .change_img {
  position: fixed;
  right: -0.4rem;
  bottom: 20%;
  width: 2rem;
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 20rem;
  -webkit-box-shadow: 0px 0px 10px white;
          box-shadow: 0px 0px 10px white;
  color: white;
}



