
.home_page .new_type .n_t_box {
  background-color: #F1F1F1;
  padding: 0.2rem 0;
  background-image: url("../assets/new_bg.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
  zoom: 1;
}
.home_page .new_type .n_t_box:after {
    content: ' ';
    display: block;
    height: 0;
    visibility: hidden;
    clear: both;
}
.home_page .new_type .n_t_box .n_t_list {
    width: 20%;
    height: 0.8rem;
    float: left;
    text-align: center;
    line-height: 0.8rem;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    font-size: 0.28rem;
    color: rgba(255, 255, 255, 0.87);
}
.home_page .new_type .n_t_box .n_t_list.on {
      -webkit-animation: neon6 1s ease-in-out infinite alternate;
              animation: neon6 1s ease-in-out infinite alternate;
      font-size: 0.32rem;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
      color: white;
}
@-webkit-keyframes neon6 {
from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #007aff, 0 0 70px #007aff, 0 0 80px #007aff, 0 0 100px #007aff, 0 0 150px #007aff;
}
to {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #ff0000, 0 0 70px #ff0000, 0 0 80px #ff0000, 0 0 100px #ff0000, 0 0 150px #ff0000;
}
}
.home_page .banner {
  width: 100%;
}
.home_page .banner .slide_img img {
    display: block;
    width: 100%;
}
.home_page .new_list {
  padding: 0 0.2rem 0.2rem;
}
.home_page .new_list .new_list_li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 0.2rem 0;
    position: relative;
    overflow: hidden;
    border-bottom: 1px solid #F1F1F1;
}
.home_page .new_list .new_list_li:last-child {
      padding-bottom: 0;
      border: 0;
}
.home_page .new_list .new_list_li .list_img {
      width: 2.4rem;
      min-height: 1.54rem;
      background-color: #e6e6e6;
      margin-right: 0.2rem;
      min-height: 1.6rem;
}
.home_page .new_list .new_list_li .list_img img {
        display: block;
        width: 100%;
}
.home_page .new_list .new_list_li .list_title {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.home_page .new_list .new_list_li .list_title .title_h2 {
        font-size: 0.28rem;
        line-height: 0.4rem;
}
.home_page .new_list .new_list_li .list_title .advertising_tag {
        display: inline-block;
        border: 1px solid red;
        font-size: 0.2rem;
        border-radius: 2px;
        color: red;
        padding: 0 0.1rem;
        margin: 0.1rem 0;
}
.home_page .new_list .new_list_li .list_title .time {
        font-size: 0.24rem;
        line-height: 0.48rem;
}



