
.details_page .details_content {
  padding: 0.2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #DEDEDE;
}
.details_page .details_content .d_img {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    margin-right: 0.2rem;
}
.details_page .details_content .d_img img {
      width: 100%;
}
.details_page .details_content .d_details {
    -webkit-box-flex: 2;
        -ms-flex: 2;
            flex: 2;
}
.details_page .details_content .d_details .d_title {
      line-height: 0.76rem;
      font-size: 0.4rem;
}
.details_page .details_content .d_details .d_title .type {
        vertical-align: top;
        line-height: 0.32rem;
        display: inline-block;
        border: 1px solid red;
        border-radius: 2px;
        font-size: 0.24rem;
        color: red;
        padding: 0 0.04rem;
}
.details_page .details_content .d_details p {
      font-size: 0.28rem;
      line-height: 0.48rem;
}
.details_page .details_content .d_details .bt_movie {
      width: 1.6rem;
      text-align: center;
      border-radius: 20px;
      line-height: 0.6rem;
      margin-top: 0.1rem;
      color: white;
      font-size: 0.28rem;
      background-color: #31F300;
}
.details_page .relevant_person {
  padding: 0.2rem;
}
.details_page .relevant_person p {
    line-height: 0.48rem;
    font-size: 0.28rem;
}
.details_page .relevant_person p .person {
      margin-right: 0.1rem;
}
.details_page .story_line {
  line-height: 0.48rem;
  font-size: 0.28rem;
}



