
.video_page .video_select {
  padding: 0 0.2rem;
  line-height: 0.64rem;
}
.video_page .video_select span {
    display: inline-block;
    padding: 0 0.2rem;
    border: 1px solid #6D6D6D;
    margin: 0.16rem 0.16rem 0 0;
    font-size: 0.28rem;
    border-radius: 20rem;
}
.video_page .video_select span.on {
      border-color: red;
      background-color: red;
      color: white;
}
.video_page .video_item {
  padding: 0.2rem;
}
.video_page .video_item .v_poster {
    margin-bottom: 0.3rem;
}
.video_page .video_item .v_poster:last-child {
      margin-bottom: 0;
}
.video_page .video_item .v_poster:last-child .v_source {
        border-bottom: 0;
}
.video_page .video_item .v_poster .v_topicImg {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.video_page .video_item .v_poster .v_topicImg img {
        width: 0.8rem;
        height: 0.8rem;
        border-radius: 50%;
        overflow: hidden;
}
.video_page .video_item .v_poster .v_topicImg p {
        margin-left: 0.2rem;
}
.video_page .video_item .v_poster .v_topicImg p span {
          display: block;
          font-size: 0.24rem;
          color: #A2A2A2;
}
.video_page .video_item .v_poster .v_topicImg p span.on {
            font-size: 0.28rem;
            color: #000;
}
.video_page .video_item .v_poster .v_title {
      color: black;
      font-size: 0.32rem;
      padding: 0.2rem 0;
      line-height: 0.4rem;
}
.video_page .video_item .v_poster .v_img {
      position: relative;
      padding-bottom: 55%;
      background-color: #eee;
}
.video_page .video_item .v_poster .v_img img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
}
.video_page .video_item .v_poster .v_source {
      height: 0.8rem;
      line-height: 0.8rem;
      border-bottom: 1px solid #F1F1F1;
}
.video_page .video_item .v_poster .v_source span {
        color: red;
}
.video_page .load_more {
  width: 3rem;
  height: 0.9rem;
  line-height: 0.9rem;
  margin: 0.2rem auto;
  line-height: 0.8rem;
  text-align: center;
  background-color: #fff;
  border: 1px solid #DCDCDC;
  border-radius: 20rem;
  font-size: 0.32rem;
}
.video_page .load_more:active {
    background-color: red;
    color: white;
}



