
.music_page .music_pic {
  min-height: 2.4rem;
  background-color: #ececec;
}
.music_page .music_pic img {
    width: 100%;
    height: 100%;
}
.music_page .m_title {
  background-color: #ECECEC;
  font-size: 0.32rem;
  padding: 0 0.2rem;
  line-height: 0.8rem;
  color: black;
}
.music_page .m_list {
  padding: 0 0.2rem 0.2rem 0.2rem;
}
.music_page .m_list li {
    height: 1.2rem;
    line-height: 1.2rem;
    font-size: 0.32rem;
    border-bottom: 1px solid #E0E0E0;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.music_page .m_list li:last-child {
      border-bottom: 0;
}
.music_page .m_list li:nth-child(1) i {
      background-color: #e80000;
}
.music_page .m_list li:nth-child(2) i {
      background-color: #ff7200;
}
.music_page .m_list li:nth-child(3) i {
      background-color: #f8b300;
}
.music_page .m_list li i {
      display: inline-block;
      margin-right: 0.2rem;
      font-size: 0.36rem;
      color: #7B7B7B;
      padding: 0 0.2rem;
}
.music_page .m_list li i.on {
        color: white;
        height: 0.52rem;
        line-height: 0.52rem;
        padding: 0 0.16rem;
        border-radius: 0.1rem;
}



