
.movie_page .movie_title {
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.32rem;
  padding: 0 0.2rem;
  font-weight: 700;
}
.movie_page .movie_item {
  width: 100%;
  padding: 0 0.1rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.movie_page .movie_item:after {
    display: block;
    clear: both;
    content: "";
    visibility: hidden;
    height: 0;
}
.movie_page .movie_item .m_list {
    float: left;
    width: 33.33%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0 0.1rem 0.2rem 0.1rem;
}
.movie_page .movie_item .m_list .m_img {
      position: relative;
      padding-bottom: 150%;
      background-color: #eee;
}
.movie_page .movie_item .m_list .m_img img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
}
.movie_page .movie_item .m_list h2 {
      height: 0.6rem;
      line-height: 0.6rem;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      font-size: 0.24rem;
}
.movie_page .load_more {
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  border-top: 1px solid #E8E8E8;
  font-size: 0.32rem;
}



