
.music_song_page .music_song_img {
  position: relative;
  padding-bottom: 60%;
  background-color: #e8e8e8;
  overflow: hidden;
}
.music_song_page .music_song_img img {
    position: absolute;
    width: 100%;
}
.music_song_page .music_song_title {
  height: 0.8rem;
  line-height: 0.8rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  padding: 0 0.2rem;
  font-size: 0.36rem;
  background-color: #EFEFEF;
}
.music_song_page .music_song_list {
  padding: 0 0.2rem 0.2rem;
}
.music_song_page .music_song_list li {
    height: 1rem;
    line-height: 1rem;
    border-bottom: 1px solid #ECECEC;
    font-size: 0.32rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.music_song_page .music_song_list li:last-child {
      border-bottom: 0;
}



